#demo-2 .big-square {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 15px;
  font-size: 23px;
  font-weight: bold;
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.5s;
}

#demo-2:hover .big-square {
  box-shadow: -8px 30px 100px -10px rgba(0, 0, 0, 0.4);
}
